import React from 'react'
import PropTypes from 'prop-types';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import {  Button, CardActionArea  } from '@mui/material';
import { Grid } from '@mui/material';
import {Link} from 'react-router-dom';
// import Background from '../../images/background.jpg'

const drawerWidth = 240;

const EmployerDashboard=(props)=> {
  console.log(props)
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
  
  // var sectionStyle = {
  //   width: "100%",
  //   height: "400px",
  //   backgroundImage: `url(${Background})`
  // };
  
  


  const drawer = (
    <Box>
      <Toolbar />
      <Divider />
      <List>
        {/* {['Dashboard', 'Post Vacancy', 'Remove Vacancy'].map((text, index) => (
          <ListItem button key={text} onClick={()=> handleOnClick('/')}>
            <ListItemText primary={text} />
          </ListItem>
        ))} */}
       <ListItem>
              <Button color="inherit" component={Link} to={"/employerDashboard"}>Dashboard</Button>
          </ListItem>
       <ListItem>
              <Button color="inherit" component={Link} to={"/postVacancy"}>Post Vacancy</Button>
          </ListItem>
       <ListItem>
              <Button color="inherit" component={Link} to={"/empProfile"}>Profile</Button>
          </ListItem>
      </List>
      <Divider />
      <List >
            <ListItem>
              <Button color="inherit" component={Link} to={"/totalVacancy"}>Total Vacancies</Button>
          </ListItem>
          <ListItem>
              <Button color="inherit" component={Link} to={"/"}>Back to Home Page</Button>
          </ListItem>
          {/* <ListItem button  >
              <Button color="inherit" component={Link} to={"/logout"}>Logout</Button>
          </ListItem> */}
      </List>
    </Box>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          ml: { sm: `${drawerWidth}px` },
          backgroundColor:'#B3D9FF'
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap component="div">
            Employer Dashboard
          </Typography>
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
            sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 }  }}
        aria-label="mailbox folders"
      >
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth,backgroundColor:'#00264D'}
          }}
          
        >
          {drawer}
        </Drawer>


        <Drawer
          variant="permanent"
          sx={{
            display: { xs: 'none', sm: 'block' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth, backgroundColor:'#00264D' },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box
        component="main"
        sx={{ flexGrow: 1, p: 3, width: { sm: `calc(100% - ${drawerWidth}px)` } }}
      >
        <Toolbar />




        {/* cards  */}
        <Grid container spacing={2} columns={16}>
          <Grid item xs={8}>
            <Grid item>


            <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
                
                <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                    Title
                </Typography>
                <Typography variant="body2" color="text.secondary">
                    text
                </Typography>
                </CardContent>
            </CardActionArea>
            
            </Card>


                    </Grid>
                </Grid>

                <Grid item xs={8}>
                    <Grid item>
                    <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
                
                <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                    Title
                </Typography>
                <Typography variant="body2" color="text.secondary">
                    text
                </Typography>
                </CardContent>
            </CardActionArea>
            
            </Card>
                    </Grid>
                </Grid>
                </Grid>

                <Grid container spacing={2} columns={16}>
                <Grid item xs={8}>
                <Grid item>
                <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
                
                <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                    Title
                </Typography>
                <Typography variant="body2" color="text.secondary">
                    text
                    </Typography>
                </CardContent>
            </CardActionArea>
            
            </Card>
                </Grid>
                </Grid>


                <Grid item xs={8}>
                <Grid item> <Card sx={{ maxWidth: 345 }}>
            <CardActionArea>
                
                <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                    Title
                </Typography>
                <Typography variant="body2" color="text.secondary">
                    text
                </Typography>
                </CardContent>
            </CardActionArea>
            
            </Card>

            
            </Grid>
                </Grid>
                </Grid>
      </Box>
    </Box>

  );
}

EmployerDashboard.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default EmployerDashboard;